const fugitiveProfile = document.querySelector(".fugitive__show");

const activeState = function () {};

const fbi = async function () {
  try {
    const respond = await fetch(
      `https://api.fbi.gov/@wanted-person/f1a6d821a6da40f5bb718b7ad844fb29`
    );
    const data = await respond.json();

    console.log(data);

    let profile = data;
    profile = {
      title: profile.title,
      uid: profile.uid,
      description: profile.description,
      details: profile.details,
      suspects: profile.suspects,
      path: profile.path,
      images: profile.images,
      files: profile.files,
      aliases: profile.aliases,
      info: [
        ["Age", profile.age_range],
        ["Eyes", profile.eyes],
        ["Hair", profile.hair],
        ["Height", profile.height_max],
        ["Languages", profile.languages],
        ["Locations", profile.locations],
        ["Nationality", profile.nationality],
        ["NCIC", profile.ncic],
        ["Occupations", profile.occupations],
        ["Place of Birth", profile.place_of_birth],
        ["Possible Countries", profile.possible_countries],
        ["Possible States", profile.possible_states],
        ["Race", profile.race],
        ["Scars and Marks", profile.scars_and_marks],
        ["Sex", profile.sex],
        ["Weight", profile.weight],
      ],
      warningM: profile.warning_message,
      rewardM: profile.reward_text,
      cautions: profile.cautions,
      remarks: profile.remarks,
      url: profile.url,
      subject: profile.subjects,
      fieldOffice: profile.field_offices,
    };

    if (!respond.ok) throw new Error(`${data.message} (${respond.status})`);
    const html = `
        <h1 class="fugitive__name">${profile.title}</h1>
        <p class="fugitive__description">
          ${profile.description}
        </p>

        <div class="fugitive__top-field">
          <div class="fugitive__top-person">
            <img src="${profile.images[0].original}" alt="" />
          </div>
          <div class="fugitive__top-poster">
            <embed src='${profile.files[0].url}' type="application/pdf"></embed>
            <a href="${profile.files[0].url}" target="_blank"> View poster </a>
          </div>
          <div class="fugitive__poster-download">
            <h3>Download Poster</h3>
            <ul>
            ${profile.files
              .map((file) => {
                return `
              <li>
                <a href="${file.url}" target="_blank">${file.name}</a>
              </li>
              `;
              })
              .join("")}
            </ul>
          </div>
        </div>
        <div class="fugitive__photo-gallery">
          <ul>
          ${profile.images
            .map((photo) => {
              return `
                <li>
                  <img src="${photo.thumb}" alt="" />
                </li>`;
            })
            .join("")}
          </ul>
        </div>
        <span class="straight__line"></span>
        
        <h2 class="fugitive__aliases">Aliases:</h2>
        <p class="fugitive__aliases-names"></p>

        <table>
        ${profile.info
          .map((f) => {
            if (f[1] === null) return;
            return `
          <tr>
            <td>${f[0]}</td>
            <td>${f[1]}</td>
          </tr>
          `;
          })
          .join("")}
       
        </table>

        <div class="fugitive__reward">
        <h2>Reward:</h2>
        <p>55555</p>
        </div>

        <div class="fugitive__remarks">
          <h2>Remarks:</h2>
          <p>${profile.remarks}</p>
        </div>

        <div class="fugitive__caution">
        
          <h2>Caution:</h2>
          <p>
            ${profile.cautions}
          </p>
        </div>

        <div class="fugitive__warning-message">
          <p>${profile.warningM}</p>
        </div>

        <div class="fugitive__tips">
          <p>
            If you have information concerning this individual, please contact
            the FBI ${
              profile.fieldOffice
            } Office at (787) 754-6000. You may also contact your
            nearest FBI office or American Embassy or Consulate.
          </p>
          <div class="fugitive__field-office">
            <p>Field Office:</p>
            <span> ${profile.fieldOffice}</span>
          </div>

          <a
            class="fugitive__tips-link"
            href="https://tips.fbi.gov/"
            target="_blank"
          >
            Submit an anonymous Tip online
          </a>
        </div>
      
    `;
    // `<ul class="preview">
    // ${data.items
    //   .map((fugitive) => {
    //     return `
    //       <li>
    //         <a href="${fugitive.id}" class="preview__link">
    //           <figure>
    //             <img src="${fugitive.images[0].large}" alt="fugitive"/>
    //           </figure>
    //           <div class="preview__data">
    //             <h4 class="preview__title">${fugitive.title}</h4>
    //             <p class="preview__crime">${fugitive.subjects[0]}</p>
    //           </div>
    //         </a>
    //       </li>
    //       `;
    //   })
    //   .join("")}
    //   </ul>`;
    fugitiveProfile.insertAdjacentHTML("beforeend", html);
  } catch (err) {
    console.log(err);
  }
};
fbi();

const crimeList = document.querySelector(".crime__list");
const navCrime = document.querySelectorAll(".crime");
const navSubcrime = document.querySelectorAll(".subcrime__list");
